import { Component, OnInit, Input, AfterContentInit } from '@angular/core';

import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';

import { Hero } from '../hero';
import { HeroService } from '../hero.service';

@Component({
  selector: 'app-hero-detail',
  templateUrl: './hero-detail.component.html',
  styleUrls: ['./hero-detail.component.css', '../app.component.css']
})
export class HeroDetailComponent implements OnInit {

  url: string;

  //  This input will get receive data from heroes.component
  //  It is selectedHero
  @Input() hero: Hero;

  constructor(
    //  holds information about the route to this instance of the HeroDetailComponent. 
    //  This component is interested in the route's bag of parameters extracted from the URL. 
    //  The "id" parameter is the id of the hero to display.
    private route: ActivatedRoute,

    // gets hero data
    private heroService: HeroService,

    //  The location is an Angular service for interacting with the browser. 
    //  You'll use it later to navigate back to the view that navigated here
    private location: Location
  ) { }

  ngOnInit() {
    this.getHero();    
  }  

  ngAfterContentInit() {
    this.url = location.pathname;
  }

  getHero(): void {
    const id = +this.route.snapshot.paramMap.get('id');
    this.hero = this.heroService.getHero(id);
  }

  goBack() {
    this.location.back();
  }

  logEvent(event) {
    console.log(event);
  }

}



